

/* page header containing search bar and button */
.header{
    text-align: center;
    margin-top: 20px;
}

/* search bar */
.header input {
    width: 35%;
    height: 30px;
    border-radius: 5px;
    border: 2px solid rgb(115, 0, 255);
    padding: 2px 5px;
    font-size: 1rem;
}

/* apply filter button */
.header button {
    float: right;
    margin-right: 5%;
    height: 32px;
    width: 100px;
    font-size: 1rem;
    font-weight: 600;
    border-radius: 4px;
    padding: 5px;
    border: none;
    background-image: linear-gradient( 109.6deg,  rgb(0, 225, 255) 11.2%, rgb(0, 255, 110) 100.2% );
    color:rgb(6, 6, 6);
    
}


/* container containing filter bar and product list */
.mainContainer{
    display: flex;
    flex-wrap: wrap;
}

.mainContainer > div {
    margin: 15px;
    width: 100%;
}



/* ============================ FilterBar.js ==================== */


/* filterbar styles */
.filterBar{
    box-shadow: 1px 0px 8px 10px rgb(157, 238, 216);;
    height: 60px;
    font-size: 1.1rem;
    background-image: linear-gradient(to top, #e6e9f0 0%, #eef1f5 100%);
    font-weight: 600;
    display: flex;
    flex-direction: row;
    color:#0b0b0b;
    border-radius: 5px;
    justify-content: space-around;
    align-items: center;
}

/* filter bar heading */
.filterBar h1{
    color:#0a1bd5;
}

/* sub heading inside filter bar */
.filterBar span{
    color:#f50404;
}

/* price range to filter items */
.priceRange{
    text-align: center;
}

/* price range slider */
.priceRange input {
    width: 100%;
    height: 5px;
}

/* category of the products */
.categoryBox{
    display: flex;
}

/* radio buttons inside category */
.categoryBox input{
    margin-left: 15px;
    cursor: pointer;
}


/* =========================== MainContent.js ===================  */

/* container for all the products */
.itemContainer{
    display:flex;
    flex-wrap: wrap;
    padding:0 6%;
}



/* =========================== ItemCard.js ===========================  */

/* single item container */
.cardContainer{
    display: flex;
    height: 330px;
    width: 250px;
    box-shadow: 0px 0px 2px 2px grey;
    margin: 2%;
    padding: 5px;
    flex-direction: column;
    font-size: 1.1rem;
    font-weight: 600;
    border-radius: 5px;
}

/* image container of product */
.imageContainer{
    height: 80%;
    width: 100%;
}

/* image within the container */
.imageContainer img{
    width: 100%;
    height: 100%;
}

/* product description */
.itemInfo{
    width: 100%;
    height: 20%;
}

/* container of name and price */
.namePrice{
    display: flex;
    height: 45%;
    text-align: center;
}

/* name of the product */
.name{
    text-align: left;
    width: 75%;
    overflow: hidden;
    text-overflow:ellipsis;
    height: 94%;
}

/* price of the product */
.price{
    width: 25%;
    color: #167d22;
    font-weight: 700;
    height: 95%;
}

/* button container within the item */
.btnContainer{
    height: 55%;
}

/* button  inside the container */
.btnContainer button{
    width: 100%;
    height: 100%;
    font-size: 1.2rem;
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    border: none;
    border-radius: 5px;
    color: white;
}

/* add to cart button */
.addBtn{
    background-image: linear-gradient( 109.6deg,  rgba(255,174,0,1) 1.2%, rgb(238, 0, 255) 100.2% );
}

/* add button on hovering over it */
.addBtn:hover{
    background-image: none;
    border: 2px solid #0e0f0e;
    background-color: white;
    color: #07f007;
    font-weight: 700;
}

